.header {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  height: 1rem;
  width: 100%;
  background-color: #ffffff;
  overflow: hidden;
  border-bottom: 0.01rem solid #e2e2e2;
}
.header .left {
    flex: 1;
}
.header .mid {
    flex: 3;
}
.header .right {
    flex: 1;
}

button:focus {
  outline: none;
}
button:active {
  opacity: .8;
}
button.btn {
  line-height: .8rem;
  font-size: .28rem;
  color: white;
  transition: .2s ease;
  white-space: nowrap;
}
button.btn_primary {
  background-color: #04BE02;
}
button.btn_danger {
  background-color: #EF4F4F;
}
button.btn_warn {
  background-color: #FFB400;
}
button.btn_small {
  font-size: .28rem;
  line-height: .6rem;
  display: inline-block;
  padding: 0 .2rem;
}
button.btn_default {
  width: 100%;
}
button.btn_large {
  width: 100%;
  line-height: 1rem;
  font-size: .36rem;
}
button.btn_circular {
  border-radius: 0.05rem;
}
button.btn_disabled {
  background-color: #cccccc;
}
button .btn_loading {
  display: inline-block;
  width: .22rem;
  height: .22rem;
  background: url("data:image/png;base64,") no-repeat;
  background-size: cover;
  animation: rotate-loading .5s linear infinite;
}
@keyframes rotate-loading {
from {
    transform: rotate(0deg);
}
to {
    transform: rotate(360deg);
}
}

.example {
  background-color: white;
  margin: .2rem;
  padding: .2rem;
  box-shadow: 0 0 .26rem 0.04rem #d0d0d0;
}
.example .title {
    display: flex;
    flex-direction: row;
    width: 100%;
    align-items: center;
    justify-content: flex-start;
    border-bottom: 0.01rem solid rgba(168, 168, 168, 0.28);
    padding: .1rem 0 .2rem 0;
    margin: 0 0 .2rem 0;
}

.mask {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  transition: .2s ease;
  background-color: rgba(0, 0, 0, 0.6);
}
